.minorBanner {
  border-radius: 20px;
  /* background: linear-gradient(
    to top,
    var(--gradient-bg) 0%,
    var(--gradient-bg) 60%,
    rgba(255, 255, 255, 0) 100%
  ); */
  border: 1px solid #dee4e7;
  padding: 40px 36px;
  padding-bottom: 50px;
  position: relative;
  overflow: hidden;
}

.minorBanner h4 {
  color: #232323;
  font-size: 40px;
  font-family: "Gilroy-semibold";
}

.minorBanner p {
  color: #464d56;
  line-height: 22px;
  font-size: 19px;
}
.minorBanner button {
  border-radius: 10px;
  background: #222525;
  /* padding: 9px 70px; */
  width: 288px;
  height: 45px;

  border: none;
}

.minorBanner button:hover {
  background-color: var(--hover-bg) !important;
}

.btnText {
  font-family: "Gilroy-medium";
  font-size: 18px;
}

.minorBannerLeft {
  position: absolute;
  left: 0;
  top: 53%;
  transform: translateY(-50%);
  z-index: 1;
}

.minorBannerRight {
  position: absolute;
  right: 0;
  top: 51%;
  transform: translateY(-50%);
  z-index: 1;
}
.minorBannerLeft,
.minorBannerRight {
  overflow: hidden;
  /* черный = видимый, прозрачный = скрытый */
  mask-image: linear-gradient(to bottom, black 20%, transparent 100%);
  -webkit-mask-image: linear-gradient(to bottom, black 20%, transparent 108%);
  mask-size: 100% 100%;
  mask-repeat: no-repeat;
}

.minorBtn {
  transition: 0.2s ease-in-out;
}
.minorBtn:hover {
  background-color: var(--btn-hover) !important;
}

/* Общий контейнер карточки */
.card {
  border-radius: 20px;
  border: 1px solid #3a3a3a;
  text-align: left;
  font-size: 16px;
}

/* GIF-ки в боковых колонках */
.prosSmallGif {
  max-width: clamp(100px, 15vw, 140px);
}

/* Стрелочки между GIF-ками */
.arrowsImg {
  width: clamp(40px, 5vw, 53px);
  height: auto;
}

/* Заголовок карточки */
.cardHeader {
  font-size: clamp(25px, 5vw, 28px);
  color: #000;
  font-size: 26px;
}

.infoBlock {
  padding: 23px 27px;
  width: 100%;
  color: #000;
  font-family: "Gilroy-medium";
  font-size: 20px;
  line-height: 26px;

  border-radius: 14px;
  border: 1px solid #dee4e7;
  background: #fff;
  text-align: left;
}

.cardDescr {
  color: #464d56;
  font-size: 16px;
  line-height: 18px; /* 112.5% */
}

.notifBlock {
  border-radius: 10px;
  border: 1px solid #e3e3e3;
  background: #f5f5f5;
  width: 433px;
  /* height: 55px; */
  padding: 9px 18px 9px 12px;
  position: relative;
  display: flex; /* <--- добавлено */
  align-items: center; /* <--- выравнивание по вертикали */
  gap: 12px; /* <--- отступ между svg и текстом, по желанию */
}
.notifText {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.notifText h6,
.notifText p {
  margin: 0; /* Убираем отступы */
  padding: 0;
}
.notifText h6 {
  color: #000;
  font-family: "Gilroy-semibold";
  /* от 12px до 16px, плавнее через 0.6vw */
  font-size: clamp(13px, 1.2vw, 16px);
}

.notifText p {
  color: #606060;
  font-family: "Gilroy-medium";
  /* от 10px до 14px, плавнее через 0.5vw */
  font-size: clamp(11px, 1vw, 14px);
}

.notifMoney {
  position: absolute;
  right: 18px;
  top: 50%;
  transform: translateY(-50%);
  color: #000;
  font-family: "Gilroy-semibold";
  /* от 13px до 18px, плавнее через 0.8vw */
  font-size: clamp(13px, 1.4vw, 18px);
}
